import React from "react";
import { Nav } from "react-bootstrap";

import {
  MdRestaurantMenu,
  MdPerson,
  MdDashboard,
  MdPieChart,
  MdShowChart,
  MdPersonAdd,
  MdDiscount,
  MdCampaign,
  MdAccountBalance,
  MdMail,
  MdStar,
  MdSettings,
  MdKeyboardArrowDown,
  MdMenu
} from 'react-icons/md'

export default function SideNav() {
  return (
    <nav className="navbar-static-side">
      <nav className="sidebar-collapse">
        <Nav id="side-menu">
          <div className="nav-header">
            <span className="logo-name">Feasti</span>
            <span className="nav-toggler" >
              <MdMenu size={24} color="#FFFFFF" />
            </span>
          </div>
          <div className="sidebar-body">
            <Nav.Link href="/" className="nav-label ">
              <span>
                <MdDashboard size={18} />
              </span>
              <span className="links-name-side" >Dashboard</span>
            </Nav.Link>
            {/* Dashboard */}

            <Nav.Link href="/users" className="nav-label">
              <span>
                <MdPerson size={18} />
              </span>
              <span className="links-name-side">Users</span>
            </Nav.Link>
            {/* Users */}

            <Nav.Item  >
              <Nav.Link className="nav-label">
                <div className="d-flex align-items-center">
                  <span className="">
                    <MdRestaurantMenu size={18} />
                  </span>
                  <span className="links-name-side">Restaurant</span>
                </div>
                <span className="">
                  <input type="checkbox" id="expandMenu" />
                  <label htmlFor="#expandMenu" >
                    <MdKeyboardArrowDown size={18} />
                  </label>
                </span>
              </Nav.Link>
              <Nav className="nav-second-level collapse" style={{ height: 20 }}>
                <Nav.Link href="/restaurant" className="nav-label">
                  <span>
                    <MdShowChart size={18} />
                  </span>
                  <span className="links-name-side">Dashboard</span>
                </Nav.Link>
                <Nav.Link href="/add_restaurant" className="nav-label">
                  <span>
                    <MdPersonAdd size={18} />
                  </span>
                  <span className="links-name-side">Add New Restaurant</span>
                </Nav.Link>
              </Nav>
            </Nav.Item>
            {/* Restaurant */}

            <Nav.Link href="/orders" className=" nav-label">
              <span>
                <MdPieChart size={18} />
              </span>
              <span className="links-name-side">Orders</span>
            </Nav.Link>
            {/* Orders */}

            <Nav.Link href="/coupons" className=" nav-label">
              <span>
                <MdDiscount size={18} />
              </span>
              <span className="links-name-side">Coupons</span>
            </Nav.Link>
            {/* Coupons */}

            <Nav.Link href="/campaign" className=" nav-label">
              <span>
                <MdCampaign size={18} />
              </span>
              <span className="links-name-side">Campaigns</span>
            </Nav.Link>
            {/* Coupons */}

            <Nav.Link href="/payments" className=" nav-label">
              <span>
                <MdAccountBalance size={18} />
              </span>
              <span className="links-name-side">Payments</span>
            </Nav.Link>
            {/* Payments */}

            <Nav.Link href="/contacts" className=" nav-label">
              <span>
                <MdMail size={18} />
              </span>
              <span className="links-name-side">Contacts and Supports</span>
            </Nav.Link>
            {/* Support */}

            <Nav.Link href="/review" className=" nav-label">
              <span>
                <MdStar size={18} />
              </span>
              <span className="links-name-side">Reviews</span>
            </Nav.Link>
            {/* Support */}

            <Nav.Link href="/setting" className=" nav-label">
              <span>
                <MdSettings size={18} />
              </span>
              <span className="links-name-side">Setting</span>
            </Nav.Link>
            {/* Settings */}
          </div>
        </Nav>
      </nav>
    </nav>
  );
}
